import React, { Component } from 'react';
import { fade, withStyles, createStyleSheet} from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Footer from '../components/Footer';
import { Typography, AppBar, Toolbar } from '@material-ui/core';
import { InputBase, TextField, OutlinedInput, Button } from '@material-ui/core';
import { callDummyAPI } from '../services/services';
import {Paper} from '@material-ui/core'
import Drawer from '@material-ui/core/Drawer';
import TotalCustomer from '../components/TotalCustomer';
import TotalOpenAR from '../components/TotalOpenAR';
import TotalOpenInvoices from '../components/TotalOpenInvoices';
import AverageDaysDelay from '../components/AverageDaysDelay';
import GetData from '../components/GetData';
import SearchPanel from '../components/SearchPanel';
import { ReactComponent as ReactLogo } from '../assets/companyLogo.svg'
import axios from 'axios';
import VoiceLogo from '../assets/voiceIcon.svg' 
import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';
import crossfilter from 'crossfilter2';
import Chatbot from '../components/Chatbot'

const styles = (theme) => ({
  root: {
    flexGrow: 1,
    paddingLeft: '1vw',
    paddingRight: '1vw',
    alignItems: "center",

  },
  image: {
    position: 'relative',
    width: "100%",
  },
  paper: {
    margin: "1%",
    padding: "1%",
    width: "80%",
    maxWidth: 800,
  },
  Typography: {
    position: 'absolute',
    top: '16px',
    right: 26,
    color: 'White',
    fontSize: 24,
  },
  searchIcon: {
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  Grid: {
    height: '70vh',
    width: '55vw',
    overflowY: 'auto',
    overflowX: 'auto',
  },
  GridSearch : {
    height: '40vh',
    width: '33vw',
    overflowY: 'auto',
    overflowX: 'auto',
  },
  Table: {
    overflow: "auto !important",
    width: "98%",
    display: "block",
    margin: "0 auto",
  },
    toolbar: {
      paddingRight: 24, // keep right padding when drawer closed
    },
  logo: {
    maxWidth: 25,
  },
  textStyle1: {
    color: '#FFFFFFA6',
    fontSize: '2.5vw',
    marginTop: '2vh',
  },
  textStyle2: {
    color: '#FFFFFFA6',
    fontSize: '1.5vw',
  },
  textfield: {
    color: '#FFFFFFA6',
    fontSize: '1.5vw',
  },
  nameInput: {
    fontSize: '1vw',
    color: '#FFFFFF',
  },
  notchedOutline: { borderWidth: '1px', borderColor: '#5DAAE0 !important' },
  searchBtn: {
    marginTop: '8vh',
    minWidth: '5vw',
    minHeight: '2.188vw',
    fontSize: '0.95vw',
    border: 'solid 0.75px #3B617C',
    // marginRight: '0.5rem',
    alignSelf: 'center',
    color: '#5DAAE0',
    '&:hover': {
      backgroundColor: '#5daae0',
      color: 'white',
    },
  },
  searchBtnDisabled: {
    minWidth: '5vw',
    minHeight: '2.188vw',
    fontSize: '0.95vw',
    border: 'solid 0.75px #3B617C',
    // marginRight: '0.5rem',
    alignSelf: 'center',
    color: 'white !important',
    background: '#FFFFFFa5',
    '&:hover': {
      cursor: 'default',
      backgroundColor: '#FFFFFFa5',
    },
  },
});


class CollectorDashboard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      response: 0,
      redirect: false,
      loading: false,
      posts: [],
      pages:1,
      rows:10,
      anchor: false,
      comp_val: "",
      comp_specif_posts: []
    };
    this.handleNameChange = this.handleNameChange.bind(this);
  }
  handleNameChange(abs) {
    this.setState({
      name: abs,
    });
  }
  toggleDrawer = (event, val) =>
  {
    this.setState({anchor: val});
  }
  handleGetStarted = (e) => {
    callDummyAPI(this.state.name).then((response) => {
      // });
      this.setState({
        response: response.data.name,
        redirect: true,
        loading: false,
      });
    });
  };
  componentWillMount()
    {
      console.log(this.props)
      this.renderPosts();
    }
    renderPosts = async () => {
            let res = await axios.get("http://localhost:8080/1705242/dummyServlet",
            {Headers:{'Content-type':'Application/Json'}});
            var poster = await res.data;
            this.setState({posts: poster})
            }
  componentDidMount() {}
  queryGet = async () => {
    var url = "http://localhost:8080/1705242/TableCompSpecif?";
    url += "&business_code=" + this.state.name
    console.log(url)
    let res = await axios.get(url, { Headers: { 'Content-type': 'Application/Json' } });
    var poster = res.data;
    this.setState({ comp_specif_posts: poster })
  }
  decided() {
     if(this.state.name.length > 2){
       this.queryGet()
       return <GetData pages={0} rows={10} posts={this.state.comp_specif_posts} selected={[]} />
     }
     else{
       return <GetData pages={0} rows={10} posts={this.state.posts} selected={[]} />
     }
  }
  render() {
    function handleName(abs){
        this.setState({ name: abs })
    }
    handleName = handleName.bind(this);
    const { classes, theme } = this.props;
    //const { compId } = this.props.PostAction;
    const dataOpen = []
    this.state.posts.map(n => {
      if (n.isOpen === 1) {
        dataOpen.push(n);
      }
    })
    var dataset = crossfilter(dataOpen)
    var totaldim1 = dataset.dimension(d => d.business_code)
    var caseNum1 = totaldim1.group().reduceSum(d => d.actual_open_amount)
    function prepareDataForTop(group) {
      var categories = [];
      var data = [];
      var gdata = group.all();
      gdata.forEach(d => {
        categories.push(d.key);
        data.push(d.value);
      });
      return {
        categories: categories,
        data: data
      }
    }
    var tempObj1 = prepareDataForTop(caseNum1)
    var abs = " "
    const options = {
      chart: {
        borderColor: 'blue',
        renderTo: 'container1',
        type: 'bar',
        backgroundColor: 'rgb(37, 44, 72)',
        height: 1200,
      },
      title: {
        text: ' ',
      },
      xAxis: {
        categories: tempObj1.categories,
        crosshair: true,
        gridLineWidth: 0,
        gridLineColor: 'rgb(37, 44, 72)',
        title: {
          text: "Business Codes",
          style: {
            color: 'blue',
          }
        }
      },
      yAxis: {
        title: {
          text: 'Count',
          gridLineWidth: 5,
          gridLineColor: 'rgb(37, 44, 72)',
          style: {
            color: 'blue',
          }
        }
      },
      credits: {
        enabled: false
      },
      tooltip: {
        headerFormat: '<span style="font-size:10px">{point.key}</span><table>',
        pointFormat: '<tr><td style="color:{series.color};padding:0">{series.name}: </td>' +
          '<td style="padding:0"><b>{point.y:.1f} </b></td></tr>',
        footerFormat: '</table>',
        shared: true,
        useHTML: false
      },
      plotOptions: {
        column: {
          pointPadding: 0.5,
          borderWidth: 6
        },
        series: {
          pointWidth: 30,
          point: {
            events: {
              click: function (e) {
                this.select(null, false)
                var selectedType = this.series.chart.getSelectedPoints();

                var filterPoints = " ";
                for (let index = 0; index < selectedType.length; index++) 
                {
                  filterPoints = selectedType[index].category;
                }
                console.log(this.state.comp_val)
                abs = String(filterPoints)
                console.log(abs)
                handleName(abs)
              }
            }
          }
        },
      },
      series: [{
        name: 'ActualOpenAmount',
        data: tempObj1.data
      },
      ]
    }
    return (
      <Grid>
      <Grid container className={classes.root} spacing={16}>
        <AppBar position="sticky">
          <Toolbar className={classes.toolbar}>
            <Grid container spacing={24}>
              <Grid item xs={0}>
                <ReactLogo />
              </Grid>
              <Grid item xs={5}>
                <Typography type="title" color="inherit" style={{fontSize: 30, fontWeight: "bold"}}>
                    ABC Company
                </Typography>
              </Grid>
              <Grid item xs={5}>
                <Typography align='center' style={{ background: 'rgb(255,120,0)', borderRadius: '0px 0px 10px 10px', width:"10vw", height:"2vh", color: 'white' }}>
                  receivables dashboard
                </Typography>
              </Grid>
              <Grid item xs={1}>
                <Grid item xs ={4} style={{alignSelf: "center", textAlign:"right"}}>
                  <React.Fragment key={'right'}>
                    <Button onClick={(event) => this.toggleDrawer(event, true)} style={{ borderRadius: '17px', backgroundColor: 'rgb(255,120,0)', width: '8vw' }} autoid='professor-button'>
                    <Typography style={{ background: 'rgb(255,120,0)', color:'white'}}>
                      Professor
                      <img src={VoiceLogo} width="20" height="20" />
                          </Typography>      
                    </Button>
                    <Drawer anchor={'right'} open={this.state.anchor} onClose={(event) => this.toggleDrawer(event, false)} style={{
                      display: 'flex',
                      flexWrap: 'wrap'}}>
                        <AppBar>
                          <Toolbar style={{height:'5px'}}>
                          <img src={VoiceLogo} width="20" height="20" />
                            <Button onClick={(event) => this.toggleDrawer(event, false)} autoid="professor-close-button" >
                              <Typography style={{color:"white"}} >
                                X
                              </Typography>
                            </Button>
                          </Toolbar>
                        </AppBar>
                        <Chatbot />
                     </Drawer>
                  </React.Fragment>
                </Grid>
              </Grid>
            </Grid>
          </Toolbar>
        </AppBar>
        <Grid container className={classes.root} spacing={8}>
          <Grid item xs={12} style={{height:'20%'}}>
            <Grid container justify="center">
              <Grid item xs={3} style={{ alignItems: "center", justify: "center", height: '17vh' }}>
                <TotalCustomer posts={this.state.posts} company={this.state.name} />
              </Grid>
              <Grid item xs={3} style={{ alignItems: "center", justify: "center", height: '17vh'  }}>
                <TotalOpenAR posts={this.state.posts} company={this.state.name} />
              </Grid>
              <Grid item xs={3} style={{ alignItems: "center", justify: "center", height: '17vh' }}>
                <AverageDaysDelay posts={this.state.posts} company={this.state.name} />
              </Grid>
              <Grid item xs={3} style={{ alignItems: "center", justify: "center", height: '17vh'  }}>
                <TotalOpenInvoices posts={this.state.posts} company={this.state.name} />
              </Grid>
            </Grid>
          </Grid>
          <Grid container spacing={8}></Grid>
            <Grid item sm={12}>
              <Grid container justify="center">
              <Grid item xs={4}>
                <Grid style={{ height: '27vh', overflowX: 'auto', overflowY: 'auto' }}>
                  <Paper style={{ backgroundColor: 'rgb(37, 44, 72)' }}>
                    <Typography variant="h5" component="h1" style={{ color: "white" }}>
                      Total Amount By Company Code
                    </Typography>
                    <div name="container1" autoid="companycode-chart">
                      <HighchartsReact highcharts={Highcharts} options={options} />
                    </div>
                  </Paper>
                  </Grid>
                <Grid className={classes.GridSearch}>
                  <div autoid="invoice-table-collector">
                      <SearchPanel data={[]} />
                  </div>
                  </Grid>
                </Grid>
                <Grid container className={classes.Grid}>
                <Grid item >
                  <Paper>
                    {
                      this.decided()
                    } 
                    </Paper>
                </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
      </Grid>
        <Grid container spacing={8}>
          <Footer />
        </Grid>
      </Grid>
    );
  }
}

export default withStyles(styles, { withTheme: true })(CollectorDashboard);
