import axios from 'axios'

const initstate={
    company: "aa",
}

const reducer = (state=initstate, action) =>
{
    const update = {...state}
    switch(action.type){
        case "UPLOAD" : {
            if(action.payload){
                return update.company= action.payload }
            else{
                return {...state}}
        }
        default: return state;
    }
}

export default reducer;