import React, { Component } from 'react';
import { Typography, AppBar, Toolbar, Paper } from '@material-ui/core';
import ArrowBack from "@material-ui/icons/ArrowBack";
import IconButton from "@material-ui/core/IconButton";
import { withStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Checkbox from '@material-ui/core/Checkbox';
import TablePagination from '@material-ui/core/TablePagination';
import Grid from '@material-ui/core/Grid';
import { callDummyAPI } from '../services/services';
import axios from 'axios'
import VoiceLogo from '../assets/voiceIcon.svg'
import Footer from '../components/Footer';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from '@material-ui/core/Button';
import { CSVLink } from 'react-csv';
import { formatter } from '../utils/formatter'
import Chatbot from '../components/Chatbot'
import Drawer from '@material-ui/core/Drawer';
import { Redirect } from 'react-router';

const styles = (theme) => ({
  root: {
    flexGrow: 1,
    paddingLeft: '1vw',
    paddingRight: '1vw',
  },
  flexContainer: {
    display: 'flex',
    alignItems: 'center',
    boxSizing: 'border-box',
    overflow: 'auto',
    overflowY: 'auto',
    overflowX: 'auto',
  },
  table: {
    // temporary right-to-left patch, waiting for
    // https://github.com/bvaughn/react-virtualized/issues/454
    '& .ReactVirtualized__Table__headerRow': {
      display: 'block',
      backgroundColor: "#1B1F38",
      flip: false,
      height: '100%',
      width: '50vw',
      overflowY: 'auto',
      overflowX: 'auto',
      margin: "0 auto",
    },
  },
  tableHeading: {
    fontWeight: "800"
  },
  tableRow: {
    cursor: 'pointer',
  },
  tableRowHover: {
    '&:hover': {
      backgroundColor: theme.palette.grey[200],
    },
  },
  tableCell: {
    flex: 1,
  },
  noClick: {
    cursor: 'initial',
  },
  Paper: {
    margin: "1%",
    padding: "1%",
    height: '60vh',
    width: '150vw',
    overflowY: 'auto',
    overflowX: 'auto',
    backgroundColor: 'rgb(37, 44, 72)'
  },  
  searchBtn: {
    marginTop: '2vh',
    minWidth: '5vw',
    minHeight: '2.188vw',
    fontSize: '0.95vw',
    border: 'solid 0.75px #3B617C',
    // marginRight: '0.5rem',
    alignSelf: 'center',
    color: '#5DAAE0',
    '&:hover': {
      backgroundColor: '#5daae0',
      color: 'white',
    },
  },
});

function pagination(query, page, rows) {
  var trimstart = (page) * rows;
  var trimEnd = trimstart + rows;

  var trimmedData = query.slice(trimstart, trimEnd);

  return trimmedData;
}
class CustomerDetails extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: this.props.location.state.name,
      cus_nu: this.props.location.state.number,
      posts: [],
      data: [],
      pages:1,
      rows:10,
      numSelected: 0,
      selected: [],
      len_sel: 0,
      isItemSelected: [],
      rowCount: 0,
      AllClick: 0,
      total_open_inv: 0,
      total_open_amt: 0,
      open_modif: false,
      disable_modif: true,
      open_expo: false,
      disable_expo: true,
      doctype: null,
      openAmt: null,
      autoFillAmt: 0,
      autoFillType: "RI",
      expoArray: [],
      anchor: false,
      shift: false
    };
    console.log(this.state.posts)
  }

  handleChangePage = (e, newPage) => {
    console.log(newPage)
    this.setState({ pages: newPage })
    this.setState({ data: pagination(this.state.posts, newPage, this.state.rows) });
  }

  handleChangeRowsPerPage = e => {
    console.log(e.target.value)
    this.setState({ rows: e.target.value, pages: 0, data: pagination(this.state.posts, 0, e.target.value) });
  }
  getIndex(value) {
    for (var i = 0; i < this.len_sel; i++) {
      if (this.state.selected[i] === value) {
        return i;
      }
    }
    return -1;
  }
  handleSelectAllClick = event => {
    console.log(event.target.checked)
    if (event.target.checked) {
      const newSelecteds = this.state.posts.map(n => n.pk_id);
      this.setState({ selected: newSelecteds, rowCount: this.state.rows, expoArray: this.state.posts, disable_expo: false }, () => console.log(this.state.selected))
      return;
    }
    else {
      this.setState({ selected: [], expoArray: [], disable_expo: true });
    }
  };
  handleClick = (event, name) => {
    const selectedIndex = this.state.selected.indexOf(name);
    let newSelected = [];
    if (selectedIndex === -1) {
      newSelected = newSelected.concat(this.state.selected, name);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(this.state.selected.slice(1));
    } else if (selectedIndex === this.state.selected.length - 1) {
      newSelected = newSelected.concat(this.state.selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        this.state.selected.slice(0, selectedIndex),
        this.state.selected.slice(selectedIndex + 1)
      );
    }
    if(newSelected.length >= 1)
    {
      this.setState({disable_expo: false})
    }
    else
    {
      this.setState({disable_expo: true})
    }
    if(newSelected.length===1)
    {
      var amt = 0;
      var type = "";
      this.state.data.map(n => {
        if(n.pk_id === name)
        {
          amt = n.actual_open_amount
          type = n.doctype
        }
      })
      this.setState({ disable_modif: false, autoFillType: type, autoFillAmt: amt});
    }
    else
    {
      this.setState({ disable_modif: true, autoFillType: "RI", autoFillAmt: 0 });
    }
    this.setState({ selected: newSelected }, () => console.log(this.state.selected));
  };
  isSelected = (name) => {
    return this.state.selected.indexOf(name) != -1
  };
  getVals = async () =>
  {
    var url = "http://localhost:8080/1705242/CustSummary?";
    url += "&customer_name=" + this.state.name;
    console.log(url)
    let res = await axios.get(url, { Headers: { 'Content-type': 'Application/Json' } });
    var poster = res.data;
    console.log(poster)
    this.setState({ total_open_inv: poster.TotalOpenAR, total_open_amt: poster.TotalOpenAmt})
  }
  componentWillMount() { 
    //var name =  this.props.match.params
    this.queryGet(this.state.name)
    this.getVals()
    //this.setState({name: name.detail, cus_nu: name.num})
  ;}
  queryGet = async (name) => {
    var url = "http://localhost:8080/1705242/CustQuery?";
    console.log(name)
    url += "&customer_name=" + name
    console.log(url)
    let res = await axios.get(url, { Headers: { 'Content-type': 'Application/Json' } });
    var poster = res.data;
    var len = res.data.length;
    console.log(poster)
    console.log(len)
    this.setState({ posts: poster })
    var dat = pagination(this.state.posts, this.state.pages, this.state.rows)
    this.setState({ data: dat });
    console.log(this.state.data);
    this.getVals()
  }
  handleGetStarted = () => {
    callDummyAPI(this.state.name).then((response) => {
      // });
      this.setState({
        response: response.data.name,
        redirect: false,
        loading: false,
      });
    });
    console.log(this.state.name)
  };
  handleClickOpen = () => {
    this.setState({open_modif :true});
  };
  handleClose = () => {
    this.setState({ open_modif: false });
  };
  toggleDrawer = (event, val) => {
    this.setState({ anchor: val });
    console.log(this.state.name)
  };
  handleClickOpenExpo = () => {
    var sender = []
    this.state.posts.map(n => {
      if (this.state.selected.indexOf(n.pk_id) > -1) {
        sender.push(n)
      }
    })
    console.log(sender)
    this.setState({ open_expo: true, expoArray: sender });
  };
  handleCloseExpo = () => {
    this.setState({ open_expo: false });
  };
  SaveModif = () =>
  {
    var url = "http://localhost:8080/1705242/ModifQuery?";
    console.log(this.state.openAmt)
    console.log(this.state.doctype)
    this.state.data.map(n => {
      if (n.pk_id == this.state.selected[0])
      {
        if(this.state.openAmt !== null){
          n.actual_open_amount = this.state.openAmt
        }
        if(this.state.doctype !== null)
        {
          n.doctype = this.state.doctype
        }
      }
    })
    url += "pk_id=" + this.state.selected[0] + "&actual_open_amount=" + this.state.openAmt + "&doctype=" + this.state.doctype;
    console.log(url)
    let res = axios.get(url);
    var poster = res.data;
    console.log(poster)
    this.queryGet(this.state.name)
    this.handleClose()
  }
  handleModifChange = ({ target }) => {
    console.log(target.value)
    console.log([target.name])
    this.setState({[target.name]: target.value});
  }
  pass = () =>
  {
    //window.location.href = "/1705242/";
    this.setState({shift: true})
  }
  redo = () => {
    this.handleCloseExpo()
  }
  render() {
    console.log('Data', this.props.match.params);
    console.log('Data', this.state.name);
    const { classes } = this.props;
    return (
      <Grid>
        {
          this.state.shift === true && (
            <Redirect to={{
              pathname: '/',
            }} />
          )
        }
      <Grid container className={classes.root} spacing={2} xs={12}>
        <AppBar position="sticky">
          <Toolbar className={classes.toolbar}>
            <Grid container spacing={24}>
              <Grid item xs='false' >
                  <IconButton aria-label="arrow" onClick={this.pass} autoid="navigation-back-button">
                  <ArrowBack style={{color:"white"}} />
                </IconButton>
              </Grid>
              <Grid item xs={5}>
                  <Typography type="title" color="inherit" style={{ fontSize: 25 }} autoid="customer-name">
                  {this.state.name}
                </Typography>
                  <Typography color="inherit" style={{ fontSize: 16 }} autoid="customer-number">
                  {this.state.cus_nu}
                </Typography>
              </Grid>
              <Grid item xs={5}>
                <Typography align='center' style={{ background: 'rgb(255,120,0)', borderRadius: '0px 0px 10px 10px', width: "10vw", height: "2vh", color: 'white' }}>
                  receivables dashboard
                </Typography>
              </Grid>
              <Grid item xs={1}>
                <Grid item xs={4} style={{ alignSelf: "center", textAlign: "right" }}>
                  <React.Fragment key={'right'}>
                    <Button onClick={(event) => this.toggleDrawer(event, true)} style={{ borderRadius: '17px', backgroundColor: 'rgb(255,120,0)', width: '8vw' }} autoid='professor-button'>
                      <Typography style={{ background: 'rgb(255,120,0)', color: 'white' }}>
                        Professor
                      <img src={VoiceLogo} width="20" height="20" />
                      </Typography>
                    </Button>
                    <Drawer anchor={'right'} open={this.state.anchor} onClose={(event) => this.toggleDrawer(event, false)} style={{
                      display: 'flex',
                      flexWrap: 'wrap'
                    }}>
                        <AppBar>
                          <Toolbar style={{ height: '5px' }}>
                            <img src={VoiceLogo} width="20" height="20" />
                            <Button onClick={(event) => this.toggleDrawer(event, false)}>
                              <Typography style={{ color: "white" }} >
                                X
                              </Typography>
                            </Button>
                          </Toolbar>
                        </AppBar>
                      <Chatbot />
                    </Drawer>
                  </React.Fragment>
                </Grid>
              </Grid>
            </Grid>
          </Toolbar>
        </AppBar>
        <Grid container style={{ height: '85vh' }} justify="center">
          <Grid item xs={12} style={{ height: '20%' }}>
            <Card style={{ backgroundColor: 'rgb(37, 44, 72)'}}>
              <CardContent>
                <AppBar position="sticky" style={{ backgroundColor: 'rgb(37, 44, 72)' }}>
              <Toolbar>
                <Grid container spacking={24}>
                  <Grid item xs={4}>
                  </Grid>
                  <Grid container>
                    <Grid item xs={1}>
                            <Button variant="outlined" color="primary" onClick={this.handleClickOpen} disabled={this.state.disable_modif} style={{ borderRadius: '12px', width: '5vw', height: '5vh', outlineColor: "lightsteelblue" }} autoid="modify-button">
                        <Typography style={{ color: 'white', fontSize: 16 }}>
                          Modify
                          </Typography>
                      </Button>
                    </Grid>
                    <Grid item xs={6}>
                            <Button variant="outlined" color="primary" onClick={this.handleClickOpenExpo} disabled={this.state.disable_expo} style={{ borderRadius: '12px', width: '5vw', height: '5vh', outline: "lightsteelblue" }} autoid="export-button" >
                            <CSVLink data={this.state.expoArray} style={{ color: "#5DAAE0" }} >
                            <Typography style={{ color: 'white', fontSize: 16 }}>
                              Export
                          </Typography>
                          </CSVLink >
                          </Button>
                    </Grid>
                    <Grid item xs={5}>
                          <Grid container spacing={24}>
                            <Grid item xs={6}>
                              <Card style={{ height: '80%', width: '99%', backgroundColor: 'rgb(37, 44, 72)' }}>
                                <CardContent>
                                    <Typography variant="h4" style={{ color: 'lightsteelblue' }} autoid="total-open-invoices-customer">
                                    {this.state.total_open_inv}
                               </Typography>
                                  <Typography style={{ color: 'lightsteelblue' }}>
                                    Total open invoice
                                  </Typography>
                                </CardContent>
                              </Card>
                            </Grid>
                            <Grid item xs={6}>
                              <Card style={{ height: '80%', width: '99%', backgroundColor: 'rgb(37, 44, 72)' }}>
                                <CardContent>
                                    <Typography variant="h4" style={{ color: 'lightsteelblue' }} autoid="total-open-amount-customer">
                                    $ {formatter(this.state.total_open_amt)}
                                </Typography>
                                  <Typography style={{ color: 'lightsteelblue' }}>
                                    Total Open Amount
                                  </Typography>
                                </CardContent>
                              </Card>
                            </Grid>
                          </Grid>
                    </Grid>
                  </Grid>
                      <Grid item xs={6}>
                        
                      </Grid>
                </Grid>
              </Toolbar>
            </AppBar>
              </CardContent>
            </Card>
            <Grid container justify="center">
              < Paper className={classes.Paper} style={{ backgroundColor: 'rgb(37, 44, 72)' }}>
                  <Table className={classes.table} autoid="invoice-table-customer">
                  <TableHead>
                    <TableRow>
                      <TableCell padding="checkbox">
                        <Checkbox
                          indeterminate={
                            this.state.numSelected > 0 &&
                            this.state.numSelected < this.state.rowCount
                          }
                          onChange={event => this.handleSelectAllClick(event)}
                          inputProps={{ "aria-label": "select all desserts" }}
                        />
                      </TableCell>
                      <TableCell style={{ color: 'white' }}>company_id</TableCell>
                      <TableCell style={{ color: 'white' }}>acct_doc_header_id</TableCell>
                      <TableCell style={{ color: 'white' }}>document_number</TableCell>
                      <TableCell style={{ color: 'white' }}>business_code</TableCell>
                      <TableCell style={{ color: 'white' }}>doctype</TableCell>
                      <TableCell style={{ color: 'white' }}>customer_number</TableCell>
                      <TableCell style={{ color: 'white' }}>fk_customer_map_id </TableCell>
                      <TableCell style={{ color: 'white' }}>customer_name </TableCell >
                      <TableCell style={{ color: 'white' }}>document_create_date</TableCell>
                      <TableCell style={{ color: 'white' }}>baseline_create_date</TableCell>
                      <TableCell style={{ color: 'white' }}>invoice_date_norm</TableCell>
                      <TableCell style={{ color: 'white' }}>invoice_id</TableCell>
                      <TableCell style={{ color: 'white' }}>total_open_amount</TableCell>
                      <TableCell style={{ color: 'white' }}> cust_payment_terms</TableCell>
                      <TableCell style={{ color: 'white' }}> clearing_date</TableCell>
                      <TableCell style={{ color: 'white' }}> isOpen</TableCell >
                      <TableCell style={{ color: 'white' }}> ship_date</TableCell>
                      <TableCell style={{ color: 'white' }}>payment_amount</TableCell>
                      <TableCell style={{ color: 'white' }}> dayspast_due</TableCell>
                      <TableCell style={{ color: 'white' }}> document_id</TableCell>
                      <TableCell style={{ color: 'white' }}> document_creation_date</TableCell>
                      <TableCell style={{ color: 'white' }}> actual_open_amount</TableCell>
                      <TableCell style={{ color: 'white' }}>invoice_age</TableCell>
                      <TableCell style={{ color: 'white' }}>invoice_amount_doc_currency</TableCell>
                      <TableCell style={{ color: 'white' }}>Predicted Payment type</TableCell>
                      <TableCell style={{ color: 'white' }}>Predicted Payment Amount</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {
                      this.state.data.map((row) => (
                        <TableRow key={row.pk_id}
                          hover
                          onClick={event => this.handleClick(event, row.pk_id)}
                          role="checkbox"
                          key={row.pk_id}
                        >
                          <TableCell padding="checkbox">
                            <Checkbox
                              checked={this.isSelected(row.pk_id)}
                            />
                          </TableCell>
                          <TableCell style={{ color: 'white' }}>{String(row.company_id)}</TableCell>
                          <TableCell style={{ color: 'white' }}>{String(row.acct_doc_header_id)}</TableCell>
                          <TableCell style={{ color: 'white' }}>{String(row.document_number)}</TableCell>
                          <TableCell style={{ color: 'white' }}>{String(row.business_code)}</TableCell>
                          <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("doctype")
                            ? String(row.doctype)
                            : String(" ")
                          }</TableCell>
                          <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("customer_number")
                            ? String(row.customer_number)
                            : String(" ")
                          }</TableCell>
                          <TableCell style={{ color: 'white' }}> {
                            row.hasOwnProperty("fk_customer_map_id")
                              ? String(row.fk_customer_map_id)
                              : String(" ")
                          }</TableCell>
                          <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("customer_name")
                            ? String(row.customer_name)
                            : String(" ")
                          }</TableCell>
                          <TableCell style={{ color: 'white' }}>
                            {row.hasOwnProperty("document_create_date")
                              ? String(row.document_create_date)
                              : String(" ")
                            }
                          </TableCell>
                          <TableCell style={{ color: 'white' }}> {
                            row.hasOwnProperty("baseline_create_date")
                              ? String(row.baseline_create_date)
                              : String(" ")
                          }</TableCell>
                          <TableCell style={{ color: 'white' }}> {row.hasOwnProperty("invoice_date_norm")
                            ? String(row.invoice_date_norm)
                            : String(" ")
                          }</TableCell>
                          <TableCell style={{ color: 'white' }}> {row.hasOwnProperty("invoice_id")
                            ? String(row.invoice_id)
                            : String(" ")
                          }</TableCell>
                          <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("total_open_amount")
                            ? String(row.total_open_amount)
                            : String(" ")
                          }</TableCell>

                          <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("cust_payment_terms")
                            ? String(row.cust_payment_terms)
                            : String(" ")
                          }</TableCell>
                          <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("clearing_date")
                            ? String(row.clearing_date)
                            : String(" ")
                          }</TableCell>
                          <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("isOpen")
                            ? String(row.isOpen)
                            : String(" ")
                          }</TableCell >
                          <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("ship_date")
                            ? String(row.ship_date)
                            : String(" ")
                          }</TableCell >
                          <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("paid_amount")
                            ? String(row.payment_amount)
                            : String(" ")
                          }</TableCell >
                          <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("dayspast_due")
                            ? String(row.dayspast_due)
                            : String(" ")
                          }</TableCell >
                          <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("document_id")
                            ? String(row.document_id)
                            : String(" ")
                          }</TableCell >
                          <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("document_creation_date")
                            ? String(row.document_creation_date)
                            : String(" ")
                          }</TableCell >
                          <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("actual_open_amount")
                            ? String(row.actual_open_amount)
                            : String(" ")
                          }</TableCell>
                          <TableCell style={{ color: 'white' }}> {row.hasOwnProperty("invoice_age")
                            ? String(row.invoice_age)
                            : String(" ")
                          }</TableCell>
                          <TableCell style={{ color: 'white' }}> {row.hasOwnProperty("invoice_amount_doc_currency")
                            ? String(row.invoice_amount_doc_currency)
                            : String(" ")
                          }</TableCell>
                          <TableCell style={{ color: 'white' }}>{this.payment_method}</TableCell>
                          <TableCell style={{ color: 'white' }}>{this.first_inv}</TableCell>
                        </TableRow>
                      ))}
                  </TableBody>
                    <TablePagination
                      component="div"
                      rowsPerPageOptions={[5, 10, 15]}
                      count={Math.ceil(this.state.posts.length / this.state.rows)}
                      page={this.state.pages}
                      onChangePage={this.handleChangePage}
                      rowsPerPage={this.state.rows}
                      onChangeRowsPerPage={this.handleChangeRowsPerPage}
                      style={{ color: "white", float: "right" }}
                      backIconButtonText={{ autoid: "pagination-button-previous-customer" }}
                      nextIconButtonText={{ autoid: "pagination-button-next-customer" }}
                      autoid="invoice-table-pagination-customer"
                    />
                </Table>
              </Paper>
            </Grid>
          </Grid>
        </Grid>
        <Grid class='pagination-wrapper'>
        </Grid>
        <Dialog open={this.state.open_modif} onClose={this.handleClose} aria-labelledby="form-dialog-title" style={{ backgroundColor: 'transparent'}}>
          <DialogTitle id="modif-dialog-title" style={{ backgroundColor: 'rgb(37, 44, 72)' }}>
            <Typography style={{ color:'lightsteelblue'}}>
              Modify
            </Typography>
            </DialogTitle >
          <DialogContent style={{ backgroundColor: 'rgb(37, 44, 72)' }}>
            <DialogContentText style={{ color:'lightsteelblue'}}>
              Modify the following
          </DialogContentText>
            <input
              type="text"
              name="openAmt"
              placeholder={this.state.autoFillAmt}
              value={this.state.openAmt}
              onChange={this.handleModifChange}
              style={{ width: '100%', color: 'default', height:'4vh' }}
                autoid= "open-amount-input"
            />
            <input
              type="text"
              name="doctype"
              placeholder={this.state.autoFillType}
              value={this.state.doctype}
              onChange={this.handleModifChange}
              style={{width:'100%', color: 'default', height:'4vh' }}
              autoid="doctype-input"
            />
          </DialogContent>
          <DialogActions >
              <Button onClick={this.handleClose} color="primary" autoid="modify-cancel-button" >
              Cancel
          </Button>
              <Button color="primary" onClick={this.SaveModif} autoid="modify-save-button" >
              Save
          </Button>
          </DialogActions>
        </Dialog>
      </Grid>
      <Grid container spacing={8}>
        <Footer />
      </Grid>
      </Grid>
    );
  }
}

export default withStyles(styles, { withTheme: true })(CustomerDetails);
