import React from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import crossfilter from 'crossfilter2';

class AverageDaysDelay extends React.Component {
    constructor() {
        super();
        this.state = {
            posts: [],
            num: [],
            company: " ",
            avgDays: 0
        }
    }
    componentWillReceiveProps(newProps) {
        this.setState({ posts: newProps.posts, company: newProps.company });
    }
    render() {
        const dataOpen = []
        this.state.posts.map(n => {
                dataOpen.push(n);
        })
        var dataset = crossfilter(dataOpen)
        var totaldim1 = dataset.dimension(d => d.business_code)
        var caseNum1 = totaldim1.group().reduceSum(d => d.dayspast_due)
        var caseNum2 = totaldim1.group().reduceCount(d => d.dayspast_due)
        var total_val = 0
        var total_count = 0
        if (this.state.company.length > 2) {
            caseNum1.all().map(n => {
                if (n.key === this.state.company)
                    total_val = n.value
            })
            caseNum2.all().map(n => {
                if (n.key === this.state.company)
                    total_count = n.value
            })
            this.state.avgDays = total_val / total_count
        }
        else {
            caseNum1.all().map(n => {
                total_val += n.value
            })
            caseNum2.all().map(n => {
                total_count += n.value
            })
            this.state.avgDays = total_val / total_count
        }
        return <div autoid="average-days-delay-text-collector" >
        <Card style={{ height: '90%', width: '98%', backgroundColor: 'rgb(37, 44, 72)'}}>
            <CardContent>
                <Typography variant="h5" style={{ color: 'lightsteelblue' }}>
                    Average Days Delay
            </Typography>
                <Typography variant="h4" style={{ color: 'White' }}>
                    {this.state.avgDays.toFixed(3)} Days
            </Typography>
            </CardContent>
        </Card>
        </div>
    }
}
export default AverageDaysDelay;