import React from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import crossfilter from 'crossfilter2';

class TotalCustomer extends React.Component
{
    constructor(props) {
        super(props);
        this.state = {
            posts: [],
            num: null,
            company: " "
        }
    }
    componentWillReceiveProps(newProps) {
        this.setState({ posts: newProps.posts, company: newProps.company });
    }
    render() {
        const dataOpen = []
        this.state.posts.map(n => {
                dataOpen.push(n);
        })
        var dataset = crossfilter(dataOpen)
        var totaldim1 = dataset.dimension(d => d.business_code)
        var caseNum1 = totaldim1.group().reduceCount(d => d.customer_name)
        console.log(this.state.company)
        if (this.state.company.length > 3){
            this.state.num= 1
        }
        else{
            this.state.num = caseNum1.all().length
        }
        return <div autoid="total-customers-text-collector" >
        <Card style={{ height: '90%', width: '98%', backgroundColor :'rgb(37, 44, 72)'}}>
        <CardContent>
                <Typography variant="h5" style={{ color: 'lightsteelblue'}}>
                Total Customers
            </Typography>
                <Typography variant="h4" style={{ color: 'White' }}>
                {this.state.num}
            </Typography>
        </CardContent>
    </Card>
    </div>
    }
}

export default TotalCustomer;