import React from 'react';
import Grid from '@material-ui/core/Grid';
import axios from 'axios'
import { Typography } from '@material-ui/core';
import { Paper, TextField  } from '@material-ui/core'
import { Button } from '@material-ui/core';
import VoiceLogo from '../assets/voiceIcon.svg' 
import UserLogo from '../assets/avatar.svg' 

class Chatbot extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            convo: [],
            message: " ",
            messages: [{
                "user" : "bot",
                "resp" : " "
            }]
        }
    }
    sendPostMsg = async () => {
        var rep = null
        console.log(this.state.message)
        await axios.post('http://localhost:4000/chat', { message: this.state.message }).then((response) => rep = response.data.message);
        console.log(rep)
        if(rep !== null)
        {
            var msgs = {
                "user": "bot",
                "resp": rep
            }
            console.log(msgs)
            this.setState(prevState => ({
                messages: [...prevState.messages, msgs]
            }))
            this.setState({ message: "" })
        }
        console.log(this.state.messages)
        
    }
    handleUser = (event) => {
        if (event.keyCode === 13) {
            console.log('enter press here! ')
        }
        else{
            this.setState({message: event.target.value})
        }
    }
    send = (event) => {
        var msgs = {
            "user": "me",
            "resp": this.state.message
        }
        this.setState(prevState => ({
            messages: [...prevState.messages, msgs]
        }), () => this.sendPostMsg())
    }
    renderOneMessage(message, i) {
            var icon = null 
            var align = {}
            if(message.user === "me"){
                icon = UserLogo
                return (
                    <div key={i}>
                        <div style={{
                            maxWidth: '350px',
                            borderRadius: '30px',
                            margin: '12px 14px',
                            width: 'inherit',
                            overflow: 'auto',
                            padding: '7px 20px',
                            display: 'inline - block',
                            fontSize: '15px',
                            wordSpacing: '0.3px',
                            color: 'white',
                            textAlign: 'end',
                        }}>
                            <img src={icon} width="20" height="20" />
                            <Typography style={{ width: '12vw', color: "White" }} autoid="human">
                                {message.resp}
                            </Typography>
                        </div>
                    </div>
                )
            }
            else{
                icon = VoiceLogo
                return (
                    <div key={i}>
                        <div style={{
                            maxWidth: '350px',
                            borderRadius: '30px',
                            margin: '12px 14px',
                            width: 'inherit',
                            overflow: 'auto',
                            padding: '7px 20px',
                            display: 'inline - block',
                            fontSize: '15px',
                            wordSpacing: '0.3px',
                            color: 'white',
                            textAlign: 'start',
                        }}>
                            <img src={icon} width="20" height="20" />
                            <Typography style={{ width: '12vw', color: "White" }} autoid="bot" >
                                {message.resp}
                            </Typography>
                        </div>
                    </div>
                )
            }

    }
    renderMessages(stateMessages) {
        if (stateMessages) {
            return stateMessages.map((message, i) => {
                return this.renderOneMessage(message, i);
            });
        }
        return null;
    }
    render(){
        return <Grid>
            <Paper style={{ backgroundColor: 'rgb(37, 44, 72)'}}>
            <Grid style={{display: "flex", justifyContent: "center", marginTop: '2rem'}} >
                    <div className="chatBox">
                        {this.renderMessages(this.state.messages)}
                    </div>   
            <Typography>
            </Typography>
            </Grid>
            </Paper>
            <Grid item xs={12} style={{
                display: 'grid',
                gridTemplateRows: 'auto 1fr auto',
                gridTemplateColumns: '100%'
            }} >
                <TextField
                    id="myInput"
                    variant="outlined"
                    onChange={(event) => { this.handleUser(event) }} 
                    autoid = "professor-input-box" />
                <Button id="myBtn" onClick={(event) => this.send(event)} autoid="professor-send-button" >Send</Button>
            </Grid>
        </Grid>
    }
}

export default Chatbot