import React from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import {formatter} from '../utils/formatter'
import crossfilter from 'crossfilter2';

class TotalOpenAR extends React.Component {
    constructor() {
        super();
        this.state = {
            posts: [],
            num: [],
            company: " "
        }
    }
    componentWillReceiveProps(newProps) {
        this.setState({ posts: newProps.posts, company: newProps.company });
    }
    render() {
        const dataOpen = []
        this.state.posts.map(n => {
            if (n.isOpen === 1) {
                dataOpen.push(n);
            }
        })
        var dataset = crossfilter(dataOpen)
        var totaldim1 = dataset.dimension(d => d.business_code)
        var caseNum1 = totaldim1.group().reduceSum(d => d.actual_open_amount)
        var total = 0
        if (this.state.company.length > 2) {
            caseNum1.all().map(n => {
                if(n.key === this.state.company)
                total = n.value
            })
        }
        else{
            caseNum1.all().map(n => {
                total += n.value
            })
        }     
        console.log(total)
        this.state.num = total
        return <div autoid="total-open-ar-text-collector" >
        <Card style={{ height: '90%', width: '98%', backgroundColor: 'rgb(37, 44, 72)', alignContent: 'centre'}}>
            <CardContent>
                <Typography variant="h5" style={{ color: 'lightsteelblue' }}>
                    Total Open AR
            </Typography>
                <Typography variant="h4" style={{ color: 'White' }}>
                    $ {formatter(this.state.num)}
            </Typography>
            </CardContent>
        </Card>
        </div>
    }
}
export default TotalOpenAR;