import React from 'react';
import { Paper, TextField } from '@material-ui/core';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import { Typography, AppBar, Toolbar } from '@material-ui/core';
import axios from 'axios';
import { Button } from '@material-ui/core';
import IconButton from '@material-ui/core/IconButton';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Grid from '@material-ui/core/Grid';
import SearchLogo from '../assets/mag-glass.svg'
import DollarLogo from '../assets/attach_money.svg'
import {formatter} from '../utils/formatter'
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import { Redirect } from 'react-router';

const style = (theme) => (
{
    searchIcon: {
        height: '100%',
        position: 'relative',
        pointerEvents: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        bgcolor: 'rgb(37, 44, 72)'
  },
  Paper:{
      width: "100%",
  }, 
  table: {
        backgroundcolor : 'rgb(37, 44, 72)',
        color: "white",
    
  }
})

class SearchPanel extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            data: this.props.data,
            adv_search: false,
            advQueryAmt: 0,
            advQueryComp: "<"
        }
    }
    componentWillMount()
    {
        this.queryDefault();
    }
    queryDefault = async () => {
        let res = await axios.get("http://localhost:8080/1705242/SearchQuery",
            { Headers: { 'Content-type': 'Application/Json' } });
        var poster = await res.data;
        this.setState({
            data: poster,
        })
    }
    queryGet = async (event) => {
        var url = "http://localhost:8080/1705242/SearchQuery?";
        var val = event.target.value
        if(val == ""){
            this.queryDefault()
        } 
        else{
        url += "customer_number=" + val + "&customer_name=" + val + "&actual_open_amount=" + val;
        console.log(url)
        let res = await axios.get(url, { Headers: { 'Content-type': 'Application/Json' } });
        var poster = res.data;
        console.log(poster)
        this.setState({ data: poster })
        }
    }
    queryAdv = async (event) =>
    {
        var url = "http://localhost:8080/1705242/AdvQuery?";
        if(this.state.advQueryAmt!=""){
            url += "comparator=" + this.state.advQueryComp + "&actual_open_amount=" + this.state.advQueryAmt;
            console.log(url)
            let res = await axios.get(url, { Headers: { 'Content-type': 'Application/Json' } });
            var poster = res.data;
            console.log(poster)
            this.setState({ data: poster })
        }
        else{
            this.queryDefault()
        }
        }
    adv_call = () =>
    {
        if (this.state.adv_search) {
            return <Grid container style={{ height: '20vh', width: '30vw' }}>
                    <Grid item xs={11}>
                    <label>
                        <Typography style={{ fontSize: 20, color:"white" }}>
                            Open Amount ($):
                        </Typography>  
                    </label>
                            <select name="cars" size="1" onChange={(event) => {this.state.advQueryComp = event.target.value}} style={{ width: '24vw', height: '5vh', borderRadius: '8px' }}>
                        <option value="<">Less than</option>
                        <option value=">">Greater than</option>
                        <option value="<=">Less than equal to</option>
                        <option value=">=">Greater than equal to</option>
                        <option value="!=">Not equal to</option>
                    </select>
                    </Grid>
                <Grid item xs={11}>
                    <label>
                        <Typography style={{fontSize: 20, color:"white"}}>
                                    Amount is:
                        </Typography>
                    </label>
                        <TextField
                            id="outlined-basic"
                            variant="outlined"
                            onChange={(event) => { this.state.advQueryAmt = event.target.value }}
                            style={{ width: '24vw', height: '5vh', backgroundColor: 'white' }} 
                            autoid="advance-search-open-amount" />
                </Grid>
                <Grid item xs={4}>
                </Grid>
                <Grid item xs={10}>
                    <Button onClick={(event) => this.toggleAdv(event)} autoid="advance-search-cancel" >
                        <Typography style={{color:"white"}}>
                        CLOSE
                        </Typography>
                    </Button>
                    <Button onClick={(event) => this.queryAdv(event)} autoid="advance-search-button" >
                        <Typography style={{ color: "white" }}>
                        SUBMIT
                        </Typography>
                    </Button>
                    </Grid>
            </Grid>
        }
        else {
                return 
        }
    }
    toggleAdv = (e) =>
    {
        if(this.state.adv_search)
        {
            this.setState({adv_search: false})
        }
        else
        {
            this.setState({ adv_search: true })
        }
    }
    handleValChange = (event) =>{
        this.setState({ val: event.target.value })
    }
    trans = (e) => {
        console.log(e);
        this.setState({ shift: true, resp_name: e.customer_name, response_num: e.customer_number})
        //window.location.href = "/customer-dashboard/" + e.customer_name + "/" + e.customer_number;
    }
    render() {
        const classes = this.props;
        return <Card minWidth="475" style={{width: '96%', backgroundColor :'rgb(37, 44, 72)'}}>
            <CardContent>
                {
                    this.state.shift === true && (
                        <Redirect to={{
                            pathname: '/customer-dashboard/',
                            state: {
                                name: this.state.resp_name,
                                number: this.state.response_num
                            },
                        }} />
                        )
                    }
                <AppBar position="static" >
                    <Toolbar>
                        <Paper className={classes.Paper}>
                        <Grid item xs = {12}>
                            <Grid container spacing={8}>
                                <Grid item xs={11}>
                                <TextField
                                    id="outlined-basic"
                                    variant="outlined"
                                    onChange={(event) => { this.queryGet(event) }} 
                                    autoid="search-text-field"
                                    style={{ width: '28vw', backgroundColor: 'rgb(37, 44, 72)' }}/>
                                    </Grid>
                                    <Grid item xs = {1}>
                                    <IconButton className={classes.iconButton} aria-label="search" onClick={(event) => this.toggleAdv(event)} autoid="advance-search-drop-down" >
                                        <img src={DollarLogo} width="20" height="28" />
                                            <ArrowDropDownIcon />
                                </IconButton>
                                </Grid>
                            </Grid>
                        </Grid>
                        </Paper>
                    </Toolbar>
                </AppBar>
                <Paper style={{ backgroundColor: 'rgb(37, 44, 72)'}}>
                    {this.adv_call()}
                        <Table className={classes.table} autoid="advance-search-table" >
                        <TableHead>
                            <TableRow>
                                <TableCell style={{ color: 'white' }}>Customer Name</TableCell>
                                <TableCell style={{ color: 'white' }}>Customer Number</TableCell>
                                <TableCell style={{ color: 'white' }}>Open Amount</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {this.state.data.map((row) => (
                                <TableRow key={row.name} onClick= {() => this.trans(row)}>
                                    <TableCell style={{ color: 'white' }}>{String(row.customer_name)}</TableCell>
                                    <TableCell style={{ color: 'white' }}>{String(row.customer_number)}</TableCell>
                                    <TableCell style={{ color: 'white' }}>{formatter(row.actual_open_amount)}</TableCell>
                                </TableRow>))}
                        </TableBody>
                    </Table>
                </Paper>
            </CardContent>
        </Card>
    }
}
export default SearchPanel;
