import React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Checkbox from '@material-ui/core/Checkbox';
import Title from './Title';
import axios from 'axios';
import TablePagination from '@material-ui/core/TablePagination';
import { Paper, Typography } from '@material-ui/core'
import Grid from '@material-ui/core/Grid';
import { AppBar, Toolbar } from '@material-ui/core';
import * as getPostByIdFunc from '../actions';

const styles = (theme) => ({
    flexContainer: {
        display: 'flex',
        alignItems: 'center',
        boxSizing: 'border-box',
        overflow: 'auto',
        overflowY: 'auto',
        overflowX: 'auto',
    },
    table: {
        // temporary right-to-left patch, waiting for
        // https://github.com/bvaughn/react-virtualized/issues/454
        '& .ReactVirtualized__Table__headerRow': {
            display: 'block',
            backgroundColor: "#1B1F38",
            flip: false,
            height:'100%',
            width:'50vw',
            overflowY:'auto',
            overflowX:'auto',
            margin: "0 auto",
        },
    },
    tableHeading: {
        fontWeight: "800"
    },
    tableRow: {
        cursor: 'pointer',
    },
    tableRowHover: {
        '&:hover': {
            backgroundColor: theme.palette.grey[200],
        },
    },
    tableCell: {
        flex: 1,
    },
    noClick: {
        cursor: 'initial',
    },
    Paper: {
        margin: "1%",
        padding: "1%",
        maxWidth: 800,
        height: '40vh',
        width: '50vw',
        overflowY: 'auto',
        overflowX: 'auto',
        backgroundColor: 'rgb(37, 44, 72)'
    }
});

function pagination(query, page, rows) {
    var trimstart = (page) * rows;
    var trimEnd = trimstart + rows;

    var trimmedData = query.slice(trimstart, trimEnd);

    var pages = Math.ceil(query.length/rows);
    return trimmedData;
}

class GetData extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            posts: this.props.posts,
            pages: this.props.pages,
            rows: this.props.rows,
            data: [],
            payment_method: "",
            first_inv: "",
            numSelected: 0,
            selected: [],
            len_sel: 0,
            isItemSelected: [],
            rowCount: 0,
            AllClick: 0,
            
        }
    }
    handleChangePage = (e, newPage) => {
        console.log(newPage)
        this.setState({ pages: newPage})
        this.setState({data: pagination(this.state.posts, newPage, this.state.rows) });
    }

    handleChangeRowsPerPage = e => {
        console.log(e.target.value)
        this.setState({ rows: e.target.value, pages: 0, data: pagination(this.state.posts, 0, e.target.value) });
    }
    getIndex(value) {
        for (var i = 0; i < this.len_sel; i++) {
            if (this.state.selected[i] === value) {
                return i;
            }
        }
        return -1;
    }
    componentWillReceiveProps(newProps)
    {
        this.setState({posts: newProps.posts},() => this.renderPosts());
    }
    renderPosts = () => {
        var dat = pagination(this.state.posts, this.state.pages, this.state.rows)
        this.setState({ data: dat }, console.log(this.state.data));
            }
    handleSelectAllClick = event => {
        console.log(event.target.checked)
        if (event.target.checked) {
            const newSelecteds = this.state.data.map(n => n.pk_id);
            this.setState({ selected: newSelecteds, rowCount: this.state.rows },() => console.log(this.state.selected))
            return;
        }
        else{
        this.setState({ selected: [] });
        }
    };
    handleClick = (event, name) => {
        const selectedIndex = this.state.selected.indexOf(name);
        let newSelected = [];

        if (selectedIndex === -1) {
            newSelected = newSelected.concat(this.state.selected, name);
        } else if (selectedIndex === 0) {
            newSelected = newSelected.concat(this.state.selected.slice(1));
        } else if (selectedIndex === this.state.selected.length - 1) {
            newSelected = newSelected.concat(this.state.selected.slice(0, -1));
        } else if (selectedIndex > 0) {
            newSelected = newSelected.concat(
                this.state.selected.slice(0, selectedIndex),
                this.state.selected.slice(selectedIndex + 1)
            );
        }
        this.setState({ selected: newSelected }, () =>console.log(this.state.selected));
    };
    isSelected = (name) => {
        return this.state.selected.indexOf(name) !== -1
    };
    predict = async (event) => {
        var sender = []
        this.state.posts.map(n => {
            if(this.state.selected.indexOf(n.pk_id) > -1){
                sender.push(n)
            } } )
        console.log(sender)
        var resp = await axios.post('http://127.0.0.1:5000/predict', { "data": sender, "id": "1705242" })
        this.state.posts.map(n => {
            resp.data.map(m => {
                if (n.acct_doc_header_id === m.acct_doc_header_id) {
                    n.payment_method = m.payment_type
                    n.pred_amt = m.predictions
                }
            })
    })
        this.renderPosts()
        console.log(resp.data)
    }
    render() {
        console.log(this.state.data)
        const classes = this.props
        return < Paper className={classes.Paper} style={{ backgroundColor: 'rgb(37, 44, 72)'}}>
            <AppBar position="static">
                <Toolbar>
                <Grid container spacking={24}>
                    <Grid item xs={2}>
                <Typography style={{ color: 'lightsteelblue', fontSize: 24}}>
                    Recent Orders
                    </Typography>
                    </Grid>
                    <Grid item xs={9}>

                    </Grid>
                    <Grid item xs={1}>
                            <button onClick={this.predict} autoid="predict-button" style={{ borderRadius: '12px', width: '5vw', height: '4vh', backgroundColor: 'Transparent', outline: 'none' }}>
                                <Typography style={{ color: 'white', fontSize: 16 }}>
                                Predict
                                </Typography>
                                </button>
                    </Grid>
                </Grid>
                </Toolbar>
                    </AppBar>
                <Table className={classes.table} autoid="invoice-table-collector">
                <TableHead>
                        <TableRow>
                        <TableCell padding="checkbox">
                        <Checkbox
                            indeterminate={
                                this.state.numSelected > 0 &&
                                this.state.numSelected < this.state.rowCount
                            }
                            onChange={event => this.handleSelectAllClick(event)}
                            inputProps={{ "aria-label": "select all desserts" }}
                        />
                         </TableCell>    
                        <TableCell style={{ color: 'white' }}>company_id</TableCell>
                        <TableCell style={{ color: 'white' }}>acct_doc_header_id</TableCell>
                        <TableCell style={{ color: 'white' }}>document_number</TableCell>
                        <TableCell style={{ color: 'white' }}>business_code</TableCell>
                        <TableCell style={{ color: 'white' }}>doctype</TableCell>
                        <TableCell style={{ color: 'white' }}>customer_number</TableCell>
                        <TableCell style={{ color: 'white' }}>fk_customer_map_id </TableCell>
                        <TableCell style={{ color: 'white' }}>customer_name </TableCell >
                        <TableCell style={{ color: 'white' }}>document_create_date</TableCell> 
                        <TableCell style={{ color: 'white' }}>baseline_create_date</TableCell>
                        <TableCell style={{ color: 'white' }}>invoice_date_norm</TableCell>
                        <TableCell style={{ color: 'white' }}>invoice_id</TableCell>
                        <TableCell style={{ color: 'white' }}>total_open_amount</TableCell>
                        <TableCell style={{ color: 'white' }}> cust_payment_terms</TableCell>
                        <TableCell style={{ color: 'white' }}> clearing_date</TableCell>
                        <TableCell style={{ color: 'white' }}> isOpen</TableCell >
                        <TableCell style={{ color: 'white' }}> ship_date</TableCell>
                        <TableCell style={{ color: 'white' }}>payment_amount</TableCell>
                        <TableCell style={{ color: 'white' }}> dayspast_due</TableCell>
                        <TableCell style={{ color: 'white' }}> document_id</TableCell>
                        <TableCell style={{ color: 'white' }}> document_creation_date</TableCell>
                        <TableCell style={{ color: 'white' }}> actual_open_amount</TableCell>
                        <TableCell style={{ color: 'white' }}>invoice_age</TableCell>
                        <TableCell style={{ color: 'white' }}>invoice_amount_doc_currency</TableCell>
                        <TableCell style={{ color: 'white' }}>Predicted Payment type</TableCell>
                        <TableCell style={{ color: 'white' }}>Predicted Payment Amount</TableCell>
                        </TableRow>
                </TableHead>
            <TableBody>
                    {
                    this.state.data.map((row) => (
                    <TableRow key={row.pk_id}
                            hover
                            onClick={event => this.handleClick(event, row.pk_id)}
                            role="checkbox"
                            key={row.pk_id}
                        >
                        <TableCell padding="checkbox">
                            <Checkbox
                            checked={this.isSelected(row.pk_id)}
                            />
                        </TableCell>
                        <TableCell style={{ color: 'white' }}>{String(row.company_id)}</TableCell>
                        <TableCell style={{ color: 'white' }}>{String(row.acct_doc_header_id)}</TableCell>
                        <TableCell style={{ color: 'white' }}>{String(row.document_number)}</TableCell>
                        <TableCell style={{ color: 'white' }}>{String(row.business_code)}</TableCell>
                        <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("doctype")
                            ? String(row.doctype)
                            : String(" ")
                        }</TableCell>
                        <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("customer_number")
                            ? String(row.customer_number)
                            : String(" ")
                        }</TableCell>
                        <TableCell style={{ color: 'white' }}> {
                            row.hasOwnProperty("fk_customer_map_id")
                                ? String(row.fk_customer_map_id )
                                : String(" ")
                        }</TableCell>
                        <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("customer_name")
                            ? String(row.customer_name)
                            : String(" ")
                        }</TableCell>
                        <TableCell style={{ color: 'white' }}>
                            {row.hasOwnProperty("document_create_date")
                                ? String(row.document_create_date)
                                : String(" ")
                            }
                        </TableCell>
                        <TableCell style={{ color: 'white' }}> {
                            row.hasOwnProperty("baseline_create_date")
                                ? String(row.baseline_create_date)
                                : String(" ")
                        }</TableCell>
                        <TableCell style={{ color: 'white' }}> {row.hasOwnProperty("invoice_date_norm")
                            ? String(row.invoice_date_norm)
                            : String(" ")
                        }</TableCell>
                        <TableCell style={{ color: 'white' }}> {row.hasOwnProperty("invoice_id")
                            ? String(row.invoice_id)
                            : String(" ")
                        }</TableCell>
                        <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("total_open_amount")
                            ? String(row.total_open_amount)
                            : String(" ")
                        }</TableCell>

                        <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("cust_payment_terms")
                            ? String(row.cust_payment_terms)
                            : String(" ")
                        }</TableCell>
                        <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("clearing_date")
                            ? String(row.clearing_date)
                            : String(" ")
                        }</TableCell>
                        <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("isOpen")
                            ? String(row.isOpen)
                            : String(" ")
                        }</TableCell >
                        <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("ship_date")
                            ? String(row.ship_date)
                            : String(" ")
                        }</TableCell >
                        <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("paid_amount")
                            ? String(row.payment_amount)
                            : String(" ")
                        }</TableCell >
                        <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("dayspast_due")
                            ? String(row.dayspast_due)
                            : String(" ")
                        }</TableCell >
                        <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("document_id")
                            ? String(row.document_id)
                            : String(" ")
                        }</TableCell >
                        <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("document_creation_date")
                            ? String(row.document_creation_date)
                            : String(" ")
                        }</TableCell >
                        <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("actual_open_amount")
                            ? String(row.actual_open_amount)
                            : String(" ")
                        }</TableCell>
                        <TableCell style={{ color: 'white' }}> {row.hasOwnProperty("invoice_age")
                            ? String(row.invoice_age)
                            : String(" ")
                        }</TableCell>
                        <TableCell style={{ color: 'white' }}> {row.hasOwnProperty("invoice_amount_doc_currency")
                            ? String(row.invoice_amount_doc_currency)
                            : String(" ")
                        }</TableCell>
                            <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("payment_method")
                                ? String(row.payment_method)
                                : String(" ")
                            }</TableCell>
                            <TableCell style={{ color: 'white' }}>{row.hasOwnProperty("pred_amt")
                                ? String(row.pred_amt)
                                : String(" ")
                            }</TableCell>
                    </TableRow>
                ))}
            </TableBody>
                <TablePagination
                    component="div"
                    rowsPerPageOptions={[5, 10, 15]}
                    count={Math.ceil(this.state.posts.length / this.state.rows)}
                    page={this.state.pages}
                    onChangePage={this.handleChangePage}
                    rowsPerPage={this.state.rows}
                    onChangeRowsPerPage={this.handleChangeRowsPerPage}
                    style={{color: "white"}}
                    backIconButtonText={{ autoid: "pagination-button-previous-collector" }}
                    nextIconButtonText={{ autoid: "pagination-button-next-collector"}}
                    autoid="invoice-table-pagination-collector"
                />
            </Table>
        </Paper>
    }
}

export default GetData;
